<template>
  <div class="todo-top">
    <input type="text" placeHolder="請輸入代辦事項，並按enter或右側+鍵確認" v-model="title" @keyup.enter="userAdd"> 
    <button><img class="plus_img" src="../assets/plus.png" @click="userAdd"></button>
  </div>                                     
</template>                                  

<script>                                 
    import {nanoid} from 'nanoid'        
    export default {
        name: "TodoTop",
        props:["todoList"],
        data(){
            return{
                title: ""           
            }
        },
        methods:{
            userAdd(){     
                                                                
                if (!this.title.trim()){                       
                    return alert("請輸入您的代辦事項!")        
                }
                console.log(this.title)
                let result = this.todoList.some(todo => todo.title === this.title)     
                if(result){
                    this.title =""              
                    return alert("此事項已在列表中，請重新輸入!")
                }
                const newTodo = {id: nanoid(), title: this.title.trim(), done: false}             
                this.$emit("addTodo", newTodo)       
                this.title =""                  
                
            }
        }
    }
</script>

<style scoped>
    .todo-top{
        display:flex;
    }
    .todo-top input{
        width: 560px;
        height: 28px;
        font-size: 14px;
        border: 1px solid #ccc;
        border-radius: 4px;
        padding: 4px 7px;
    }
    button{
        background: #FFF492;
        border-radius: 4px;
        border: 1px solid #FFF492;
        width: 26px;
        height: 26px;
        margin: auto 5px;
    }
    .plus_img{
        display:block;
        width: 14px;
        height: 14px;
       
    }
    .todo-top input:focus{
        outline: none;            
        border-color:#F7DB92;
        box-shadow: inset 1px 1px 8px 1px #FFFBDB;         
    }   
</style>
