<template>
    <div class="todo-container">
            <Top @addTodo="addTodo" :todoList="todoList"></Top>                               
            <List 
                :todoList="todoList" 
            >                                       
            </List>                                 
            <Bottom 
                :todoList="todoList"
                @checkAllTodo="checkAllTodo"
                @deleteDoneTodo="deleteDoneTodo"
            >
            </Bottom>                                                        
    </div>
    <div class="coverFlow">
            <img src="./assets/01.jpg">
            <img src="./assets/02.jpg">
            <img src="./assets/03.jpg">
            <img src="./assets/04.jpg">
            <img src="./assets/05.jpg">
    </div>
</template>

<script>
    import pubsub from 'pubsub-js'
    import TodoTop  from './components/Top'
    import TodoList from './components/List'        
    import TodoBottom from './components/Bottom'
    
    export default {
        name: "App",
        components:{
            Top : TodoTop,
            List : TodoList,
            Bottom: TodoBottom
        },
        data(){
            return{
                todoList:JSON.parse(localStorage.getItem("todoList")) || []
            }
        },
        methods:{
            addTodo(todo){                     
                this.todoList.unshift(todo)
            },
            checkTodo(checkedChangeId){                 
                this.todoList.forEach((todo)=>{             
                    if(todo.id === checkedChangeId){        
                        todo.done = !todo.done              
                    }
                })
            },                                              
            deleteTodo(_,deletedId){                              
                this.todoList = this.todoList.filter((todo)=>{      
                    return todo.id !== deletedId                   
                })
            },
            checkAllTodo(allChecked){                       
                this.todoList.forEach((todo)=>{                     
                    todo.done = allChecked                          
                })
            },
            deleteDoneTodo(){                                       
                this.todoList = this.todoList.filter((todo)=>{
                    return !todo.done                                   
                })
            },
            updateTodo(updatedTodo){        
                this.todoList.forEach((todo)=>{                    
                    if(todo.id === updatedTodo.id ){                 
                        todo.title = updatedTodo.title
                    } 
                })
            }
        },
        watch:{                     
            todoList:{
                deep: true,                
                handler(newValue){
                    localStorage.setItem("todoList",JSON.stringify(newValue))       
                }
            }
        },
        mounted(){              
            this.bus.on("checkTodo",this.checkTodo)             
            pubsub.subscribe("deleteItem",this.deleteTodo)      
            this.bus.on("updateTodo",this.updateTodo)
        }
    }
</script>

<style >                     
    .btn{
        display: inline-block;
        padding: 4px 12px;
        margin-bottom: 0;
        font-size: 14px;
        line-height: 20px;
        text-align: center;
        vertical-align: middle;
        cursor: pointer;
        border-radius: 4px;
        border: 3px solid #F7DBDF;
        background-color: #F7DBDF;
    }
    .btn-deleteDone:hover{
        color:#080707;
        background: #E795A0;
        border: 3px solid #E795A0;
    }
    .btn-delete{
        color: #080707;
        background-color: #F7DBDF;
        border: 1px solid #F7DBDF;
    }
    .btn-edit{
        color: #080707;
        background-color: #FFF492;
        border: 1px solid #FFF492;
        margin-right: 5px;
    }
    .btn-delete:hover{
        color:#080707;
        background: #E795A0;
    }
    .todo-container{
        width: 600px;
        margin: 0 auto;
        padding: 10px;
        border: 1px solid #ddd;
        border-radius: 5px;
    }
    .coverFlow>img{
        z-index: -100;
        display: block;
        width: 100%;
        height: 300px;
        object-fit:cover;
        position: fixed;
        left:0px;
        bottom:0px;
        opacity: 0;
        animation: silder 60s ease-in-out infinite;
    }
    @keyframes silder {
        5%{
            opacity: .15;
        }
        15%{
            opacity: .09;
        }
        20%{
            opacity: 0;
        }
    }
    .coverFlow>img:nth-child(5){
        animation-delay: 48s;
    }
    .coverFlow>img:nth-child(4){
        animation-delay: 36s;
    }
    .coverFlow>img:nth-child(3){
        animation-delay: 24s;
    }
    .coverFlow>img:nth-child(2){
        animation-delay: 12s;
    }
    .coverFlow>img:nth-child(1){
        animation-delay: 0s;
    }
</style>