<template>
    <transition name="todoTransition"  appear>
        <li>
            <label>                                         
                <input type="checkbox" :checked="todo.done" @click="handleCheck(todo.id)">
                <span v-show="!todo.isEdit">{{todo.title}}</span> 
                <input  
                    type="text"
                    v-show="todo.isEdit" 
                    :value="todo.title" 
                    @blur="handleBlur(todo,$event)"
                    ref="inputTitle"
                >  
            </label>
            <button class="btn btn-delete" @click="handleDelete(todo.id)">刪除</button>
            <button class="btn btn-edit" @click="handleEdit(todo)" v-show="!todo.isEdit">編輯</button>
        </li>
    </transition>
</template>

<script>
    import pubsub from "pubsub-js"
    export default {
        name: "TodoItem",
        props:["todo"],      
        methods:{
            handleCheck(id){                    
                this.bus.emit("checkTodo",id)       
            },                                      

            handleDelete(id){                   
                if(confirm("您確定要刪除嗎?")){      
                    pubsub.publish("deleteItem",id)     
                }
            },
            handleEdit(todo){                    
                if(Object.prototype.hasOwnProperty.call(todo, "isEdit")){    
                    todo.isEdit = true                   
                }                                      
                else{
                    todo["isEdit"] = true               
                }
                this.$nextTick(function(){                  
                    this.$refs.inputTitle.focus()               
                })
            },
            handleBlur(todo,event){             
                todo.isEdit = false                
                if(!event.target.value.trim()){
                   return  alert("輸入框不能空白!")
                }
                let updatedTodo = {                
                    id: todo.id,
                    title: event.target.value
            }                                           
                this.bus.emit("updateTodo",updatedTodo)     
            }
        }
    }
</script>

<style scoped>
    li{
        list-style: none;       
        height: 36px;
        padding: 5px;
        border-bottom: 1px solid #ddd;
    }
    li label{
        float: left;
        cursor: pointer;       
    }
    li label input{
        margin-right: 6px;
        position: relative;
        top: 8px;
    }
    li label span{
        position: relative;
        top: 6px;
    }
    li button{
        float: right;
        display:none;          
        margin-top: 3px;
    }
    li:before{
        content: initial;      
    }
    li:last-child{             
        border-bottom: none;   
    }
    li:hover{                       
        background: #f6f6f6a2;
    }
    li:hover button {                     
        display: block;
    }
    .todoTransition-enter-active{
        animation: todoAnimation 1s ease;
    }
    .todoTransition-leave-active{
        animation: todoAnimation 1s ease reverse;
    }
    @keyframes todoAnimation{
        from{
            transform: translateX(100%);
        }
        to{
            transform: translateX(0px);
        }
    }
</style>
