<template>
  <div class="todo-bottom" v-show="totalTodo">     
    <label>                                         
        <input type="checkbox" :checked="allDone" @click="checkAll">全選  
    </label>
    <span class="bottom-info">已完成<span class="total-num">{{doneTotal}}</span></span>
    <span class="bottom-info">全部<span class="total-num">{{totalTodo}}</span></span>
    <button class="btn btn-deleteDone" @click="deleteDone">清除已完成的事項</button>
  </div>
</template>

<script>
    export default {
        name:"TodoBottom",
        props:["todoList"],
        methods:{
            checkAll(event){                                    
                this.$emit("checkAllTodo",event.target.checked)      
            },
            deleteDone(){                                       
                if(confirm("您確定要刪除嗎?")){                         
                    this.$emit("deleteDoneTodo")                        
                }
            }    
        },
        computed:{       
            totalTodo(){        
                return this.todoList.length
            },
            doneTotal(){
                return this.todoList.reduce((acc,cv)=>{        
                    return acc + (cv.done? 1 : 0)              
                },0)                                           
            },                                                  
            allDone:{
                get(){
                    return this.doneTotal === this.totalTodo &&  this.totalTodo > 0      
                },                                                                                            
                set(value){
                    this.$emit("checkAllTodo",value)
                }
            }                                                                                                                                                         
        }
    }
</script>

<style scoped>
    .todo-bottom{
        height: 40px;
        line-height: 40px;
        padding-left: 6px;
        margin-top: 5px;
    }
    .todo-bottom label{
        display: inline-block;
        margin: 0 10px 0 0;
        cursor: pointer;
    }
    .todo-bottom label input{
        position: relative;
        top: -1px;
        vertical-align: middle;
        margin-right: 5px;
    }
    .bottom-info{
        background: #F6D580;
        padding: 6px 2px 6px 10px;
        margin-right: 20px;
        border-radius: 20px;
        font-size: 14px;
    }
    .total-num{
        background:white;
        padding: 4px 8px;
        margin: 0 0 0 10px;
        border-radius: 30px;
    }
    .todo-bottom button{
        float: right;
        margin-top: 5px;
    }
</style>
