<template>
    <ul class="todo-list">
        <Item 
            v-for="thing in todoList" 
            :key="thing.id" 
            :todo="thing"               
            >                                       
        </Item>                                     
    </ul>                                           

</template>                                         

<script>
    import TodoItem from './Item'

    export default {
        name: "TodoList",
        components:{
            Item : TodoItem
        },
        props:["todoList"]      
    }
</script>

<style scoped>
    .todo-list{
        margin-left: 0px;
        border: 1px solid #ddd;
        border-radius: 2px;
        padding: 0px;
    }
</style>
